import React, { Component } from 'react';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

import MTO from './MTO';
import Brock from './Brock Logo';
import SciTech from './SciTech Logo';
import AM from './AirMiles Logo';
import EXP from './Experian Logo';

class Resume extends Component {
  render() {

    if(this.props.data){
      var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map(function(education){
        return <div>
        <h3 className="vertical-timeline-element-title" >{education.school}</h3>
        <h5 className="vertical-timeline-element-subtitle">{education.degree}</h5>
        <h7 style={{"color":"#615c5b"}}>{education.description}</h7>
        </div>
      })
      var educationyears = this.props.data.education.map(function(education){
        return  <h3 className="date" style={{"color":"#FFFFFF"}}>{education.graduated}</h3>
      })
      var workyears = this.props.data.work.map(function(work){
        return  <h3 className="date" style={{"color":"#FFFFFF"}}>{work.years}</h3>
      })
      var work = this.props.data.work.map(function(work){
        var description=[];
        for(var i=0;i<2;i++){
          if(work.description[i]){
            description[i]=(<div><h7 style={{"color":"#615c5b"}}>{work.description[i]}</h7></div>)
          }
        }
        return <div>
            <h3 className="vertical-timeline-element-title" style={{"margin-bottom":"1.5%"}}>{work.company}</h3>
            <h5 className="vertical-timeline-element-subtitle" style={{"margin-bottom":"0.1%"}}>{work.title}</h5>
            <h6 className="vertical-timeline-element-subtitle" style={{"margin-bottom":"2%"}}>{work.location}</h6>
            {description}
        </div>
      })
      var skills = this.props.data.skills.map(function(skills){
        // var className = 'bar-expand '+skills.name.toLowerCase();
        {/* <span style={{width:skills.level}}className={className}></span> */}
        return (
        <div key={skills.name}>
          <div className="container">
            <h1    style={{"font": "150% 'opensans-bold', sans-serif", "color": "#ffffff"}}>{skills.name}</h1>
              <div className="progressbar-container">
                <div className="progressbar-complete" style={{width: `${skills.level}`}}>
                    <div className="progressbar-liquid"></div>
                </div>
                <span className="progress"> {skills.level}</span>
              </div>
          </div>
        </div>
        )
      })
      
      var EXP1 = work[0];
      var AM1 = work[1];
      var MTO1 = work[2];
      var MTO2 = work[3];
      var MOH1 = work[4];
      var MOH2 = work[5];
      var BrockEd = education[0];
      var ChingEd = education[1];
      var ExpDate = workyears[0];
      var AMDate = workyears[1];
      var MTO1Date = workyears[2];
      var MTO2Date = workyears[3];
      var MOH1Date = workyears[4];
      var MOH2Date = workyears[5];
      var BrockEdDate = educationyears[0];
      var ChingEdDate = educationyears[1];
    }

    return (
      <div id="resume">         
        <h1 style={{'color': '#fff', 'text-align':'center', 'margin-top':'5%','margin-bottom':'5%'}}>Work Experience</h1>
        <VerticalTimeline>
         <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={ExpDate}
            iconStyle={{ background: '#004293', color: '#fff'}}
            icon={<EXP />}
          >
            {EXP1}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={AMDate}
            iconStyle={{ background: '#135582', color: '#fff'}}
            icon={<AM />}
          >
            {AM1}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={MTO1Date}
            iconStyle={{ background: '#23bbe7', color: '#fff' }}
            icon={<MTO />}
          >
            {MTO1}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={MTO2Date}
            iconStyle={{ background: '#23bbe7', color: '#fff' }}
            icon={<MTO />}
          >
            {MTO2}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={MOH1Date}
            iconStyle={{ background: '#a1419d', color: '#fff' }}
            icon={<MTO />}
          >
            {MOH1}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={MOH2Date}
            iconStyle={{ background: '#a1419d', color: '#fff' }}
            icon={<MTO />}
          >
            {MOH2}
          </VerticalTimelineElement>
          </VerticalTimeline>
          <h1 style={{'color': '#fff', 'text-align':'center', 'margin-top':'5%','margin-bottom':'5%'}}>Education</h1>
          <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={BrockEdDate}
            iconStyle={{ background: '#eb1b23', color: '#fff' }}
            icon={<Brock />}
          >
            {BrockEd}
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={ChingEdDate}
            iconStyle={{ background: '#00853e', color: '#fff' }}
            icon={<SciTech />}
          >
            {ChingEd}
          </VerticalTimelineElement>
        </VerticalTimeline>
        {/* <div style={{'margin-top':'10%','margin-bottom':'2%'}} className="row skill">
            <div className="three columns header-col">
              <h1 style={{'color': '#fff'}}><span>Skills</span></h1>
            </div>
            <div className="nine columns main-col">
              {/* { <p>{skillmessage}</p> } */}
              {/* <div className="bars">
                <ul className="skills"> */}
                  {/*skills*/}
                {/* </ul>
              </div> *a/}
            </div>
        </div> */}
      </div>)










  //     <section id="resume">

  //     <div className="row education">
  //        <div className="three columns header-col">
  //           <h1><span>Education</span></h1>
  //        </div>

  //        <div className="nine columns main-col">
  //           <div className="row item">
  //              <div className="twelve columns">
  //                {education}
  //              </div>
  //           </div>
  //        </div>
  //     </div>


  //     <div className="row work">

  //        <div className="three columns header-col">
  //           <h1><span>Work</span></h1>
  //        </div>

  //        <div className="nine columns main-col">
  //         {work}
  //       </div>
  //   </div>



  //     <div className="row skill">

  //        <div className="three columns header-col">
  //           <h1><span>Skills</span></h1>
  //        </div>

  //        <div className="nine columns main-col">

  //           <p>{skillmessage}
  //           </p>

	// 			<div className="bars">
	// 			   <ul className="skills">
	// 				  {skills}
	// 				</ul>
	// 			</div>
	// 		</div>
  //     </div>
  //  </section>
  //   );
  }
}

export default Resume;
